@font-face {
  font-family: DrukCond;
  src: url("drukxcond-super-trial-webfont.8598d86a.woff") format("woff2"), url("drukxcond-super-trial-webfont.20178096.woff2") format("woff");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

.desktop {
  display: block;
}

.mobile {
  display: none;
}

html, body {
  color: #fff;
  background-color: #fff;
}

#app {
  height: 100vh;
}

.can-scroll {
  height: auto;
}

.background {
  width: 100%;
  height: 100vh;
  position: fixed;
}

.background img {
  width: 100%;
  opacity: 0;
  transition: opacity 2s;
}

.background-scroll {
  height: 100%;
  height: calc(100% - 100px);
  transition: all .6s ease-in-out;
  position: absolute;
  top: 50px;
  right: 20px;
  transform: translateY(-110vh);
}

.background-scroll-line {
  width: 1px;
  background-color: #fff;
}

.background-scroll-line-top {
  position: absolute;
}

.background-scroll-line-bottom {
  position: absolute;
  bottom: 0;
}

.background-scroll-text {
  transform-origin: center;
  font-family: aktiv-grotesk-extended, sans-serif;
  font-size: 10px;
  font-style: normal;
  font-weight: 700;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
  transform: translateY(-100%)rotateZ(90deg);
}

.logo-container {
  width: 100%;
  height: 100vh;
  z-index: 2;
  position: fixed;
  top: 0;
}

.logo {
  width: 512px;
  height: 215px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.logo-mask {
  width: 0%;
  height: 100%;
  background-color: #fff;
  transition: all 1s ease-in-out;
  position: absolute;
  -webkit-mask-image: url("florencesq_logo.bb5edf84.svg");
  -webkit-mask-position: 0;
  -webkit-mask-size: 512px 100%;
  mask-size: 512px 100%;
  -webkit-mask-repeat: no-repeat;
}

.logo img {
  height: 100%;
}

.content {
  width: 100%;
  height: 200%;
  text-align: center;
  z-index: 2;
  padding-top: 100vh;
  position: relative;
}

.content-slogan {
  width: 100%;
  height: auto;
  margin: 100px 0 0;
  position: relative;
}

.content-slogan-inner {
  position: relative;
}

.content-slogan-inner h1 {
  letter-spacing: 3px;
  font-family: DrukCond, sans-serif;
  font-size: 160px;
  font-weight: 100;
  line-height: 135px;
}

.content-slogan-inner img {
  width: 100%;
}

.content-date {
  margin: 60vh 0;
  font-family: aktiv-grotesk-extended, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
}

.contact {
  text-align: center;
  padding: 0 0 40px;
  font-family: aktiv-grotesk-extended, sans-serif;
  font-style: normal;
  font-weight: 400;
}

.contact-logo {
  margin-bottom: 20px;
}

.contact-logo-kf img {
  height: 40px;
}

.contact-logo-cbre {
  margin-top: 10px;
}

.contact-logo-cbre img {
  height: 30px;
}

.contact h2 {
  letter-spacing: 1.5px;
  margin-bottom: 50px;
  font-family: DrukCond, sans-serif;
  font-size: 70px;
}

.contact-list {
  padding: 0;
  list-style: none;
}

.contact-credit {
  margin-top: 60px;
}

a, a:active {
  color: #fff;
  text-decoration: none;
}

@media (max-width: 767.98px) {
  .desktop {
    display: none;
  }

  .mobile {
    display: block;
  }

  .content-slogan-inner h1 {
    font-size: 90px;
    line-height: 85px;
  }

  .background img {
    width: auto;
    height: 100%;
  }

  .logo {
    width: 256px;
    height: 107px;
  }

  .contact-agent:first-child {
    margin-bottom: 50px;
  }

  .logo-mask {
    -webkit-mask-size: 256px 100%;
    mask-size: 256px 100%;
  }
}

@media (hover: hover) {
  .contact a:hover {
    color: #0c68b1;
  }
}

